    <span class="hero__content">About</span>
  </h1>
  <header-bar />
  <article class="mt-plus" itemscope="" itemtype="http://schema.org/Person">
    <section class="c-centering">
      <h2 class="message">
        Delusion, Play, Gamification.
      </h2>
      <p class="sub-title">
        AIXRSF<br>
      </p>

      <div class="development mt-plus mb-100">
        <div class="skill">
          <img class="skill__image" src="@/assets/image/skill/skill-1.jpg" alt="">
          <div class="skill__box">
            <h2 class="title"></h2>
            <ul class="description">
              <li></li>
              <li><b></b>:A/B</li>
              <li><b></b></li>
            </ul>
          </div>
        </div>

        <div class="skill">
          <img class="skill__image" src="@/assets/image/skill/skill-2.jpg" alt="">
          <div class="skill__box">
            <h2 class="title"></h2>
            <ul class="description">
              <li>toBtoCWeb</li>
              <li><b>UX</b>:</li>
              <li><b>UI</b>:UI</li>
            </ul>
          </div>
        </div>

        <div class="skill">
          <img class="skill__image" src="@/assets/image/skill/skill-3.jpg" alt="">
          <div class="skill__box">
            <h2 class="title"></h2>
            <ul class="description">
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </div>
        </div>
      </div>
    </section>

    <section class="aboutme">
      <div class="profiles c-centering c-shadow">
        <img class="akinen" :src="akinen" alt="">
        <div class="profile">
          <h2 class="profile__name">AKINORI OZAWA</h2>
          <p class="profile__discription">PRODUCT DESIGNER</p>
          <p class="profile__discription">
            LobiUIStockrInsurTechDX
          </p>
        </div>
      </div>
    </section>
  </article>

  <footer-area />
</div>
</template>

<script>
import HeaderArea from '@/components/HeaderArea'
import FooterArea from '@/components/FooterArea'
import HeaderBar from '../HeaderBar.vue'

export default {
  name: 'About',
  data () {
    return {
      bg_about: require('@/assets/image/bg_sunset.jpg'),
      akinen: require('@/assets/image/akinen_illust.png')
    }
  },
  components: {
    HeaderArea,
    FooterArea,
    HeaderBar
  }
}
</script>

<style scoped lang="stylus">
@import "../../assets/css/common.styl"

.message
  margin-bottom -20px
  font-size 24px
  font-weight 700
  text-align center
  line-height 1.8
  letter-spacing 0.1rem

// 
.skill
  width 100%
  text-align center
  box-sizing border-box
  overflow hidden

  &__image
    width 240px
    height 240px
    object-fit cover
    border-radius 16px

  &__box
    padding 20px

.title
  font-size 20px
  font-weight 700
  margin-bottom 12px
  text-align center

.description
  > li
    text-align left
    list-style-type disc
    line-height 1.8
    margin 0 0 6px 20px

.development
  display flex
  flex-direction row
  border-radius 20px
  background-color white

  @media (max-width: 960px)
    flex-direction column

  > .skill
    &:last-child
      margin 0

    &:not(:last-child)
      margin 0 20px 0 0

    @media (max-width: 960px)
      &:not(:last-child)
          margin 0 0 20px 0

// 
.aboutme
  width 100%
  padding 88px 0 44px
  background-color #222

.profiles
  position relative
  padding 20px
  border-radius 20px
  background-color white
  box-sizing border-box

  @media (max-width: 768px)
    height auto
    padding 20px

.akinen
  position absolute
  top 20px
  left 20px
  width 200px
  height 200px
  border-radius 16px

  @media (max-width: 768px)
    position relative
    top 0
    left 0
    margin 0 auto
    display block

.profile
  margin 20px 20px 20px 240px

  @media (max-width: 768px)
    height auto
    margin 0

  &__name
    font-size 32px
    font-weight bold
    line-height 2rem
    letter-spacing 0.2rem

    @media (max-width: 768px)
      text-align center
      margin-top 20px

  &__discription
    padding-top 8px
    line-height 2.0
    letter-spacing 0.04rem

    @media (max-width: 768px)
      text-align center
</style>



